
import util from '@/libs/util'
import modal from '@/libs/modal.mixin'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'

import upload from 'c%/upload'
import Tag from 'c%/Tag.vue'
import SelectArrange from './select-arrange.vue'
import SelectFile from 'c%/SelectFile.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


const emptyObject = {
bid: 0,
int_day: 0,
int_start_hour: 0,
int_end_hour: 0,
teach_eid: 0,
lesson_type: -1,
cid: 0,
sids: 0,
title: '',
content: '',
is_push: 1,
lid: 0,
sj_id: 0,
ca_id: 0,
course_prepare_attachment: []
}

export default{
mixins: [ common, globals, modal ],
directives: {
TransferDom
},
components: {
Tag,
upload,
Popup,
Group,
Cell,
XInput,
XTextarea,
Flexbox,
XButton,
FlexboxItem,
SelectFile,
SelectArrange
},
data () {
return {
info: util.copy(emptyObject),
tags: [],
}
},
computed: {
style () {
return {
top: this.headerHeight +'px',
height: `calc(100% - ${this.headerHeight}px)`
}
},
classTime () {
let result = util.sprintf('%s~%s',this.$filter('int_hour')(this.info.int_start_hour),this.$filter('int_hour')(this.info.int_end_hour))
return result
},
upload_header(){
return {
'x-token':this.$store.state.user.token,
'x-file-key':'file'
}
},
upload_post(){
return {
mod:'prepare'
}
},
objName () {
let type = parseInt(this.info.lesson_type)
let map_type = {0:'班课',1:'一对一',2:'一对多'}

return map_type[type]||''
}
},
methods: {
handleRemove (item) {
let index = this.uploadList.indexOf(item)
if(index > -1){
this.uploadList.splice(index,1)
}
},
ok () {
if(this.check()){
let params = util.copy(this.info),
[uri,method,msg] = this.modal$.action=='add'?['course_prepares','post','添加成功！']:['course_prepares/'+this.info.cp_id,'put','修改成功！']

params.bid = this.bid$[0]
params.course_prepare_attachment = this.uploadList

this.$rest(uri)
[method](params)
.success(data=>{
this.toast(msg,'success')
this.$emit('on-success')
this.close()
}).error(response=>{
this.toast(response.body.message||'操作失败','warn')
})

}
},
check () {
let info = this.info
if(info.lesson_type==-1){
this.toast('请选择备课对象','warn')
return false
}
if(!info.int_day){
this.toast('请选择上课日期','warn')
return false
}
if(!info.int_start_hour||!info.int_end_hour){
this.toast('请选择上课时间','warn')
return false
}
if(/^\s*$/.test(info.title)){
this.toast('请输入备课标题','warn')
return false
}
if(/^\s*$/.test(info.content)){
this.toast('请输入备课内容','warn')
return false
}

return true
},
chooseArrange () {
this.$refs.arrange
.set('currentItem',this.info)
.show('','add')
},
setObject (row) {
this.info.ca_id = row.ca_id
this.info.lesson_type = row.lesson_type
this.tags = []

this.setArrangeInfo(row)
if(row.lesson_type==0){
this.info.cid = row.cid
this.info.eid = row.eid
this.info.lid = row.lid
this.info.sids = []

this.tags.push({
name: row.one_class.class_name,
id: row.cid
})
}else{
this.info.cid = 0
this.info.lid = row.lid
this.info.sid = row.lesson_type==1?row.students[0].sid:0
this.info.sids = []
let list = row.students.filter(s=>s.sid>0)

if(row.students.length){
list.forEach((s,i)=>{
let obj = {
name: s.student.student_name,
id: s.student.sid
}
this.tags.push(obj)
if(row.lesson_type==2){
this.info.sids.push(s.sid)
}
})
}
}

},
setArrangeInfo (arrange) {
this.info.int_day = arrange.int_day
this.info.int_start_hour = arrange.int_start_hour
this.info.int_end_hour = arrange.int_end_hour
this.info.teach_eid = arrange.teach_eid
this.info.sj_id = arrange.sj_id
},
}
}



