
import util from '@/libs/util'
import modal from '@/libs/modal.mixin'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'

import Tag from 'c%/Tag.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import ArrangeFilter from 'c%/Filter.vue'

const dictList = [
{com: 'SelectLesson',model: 'lid', title: '课程',default: 0},
{gvar: 'subjects',model: 'sj_id', idField: 'sj_id', textField:'subject_name', title: '科目',default: 0},
{gvar: 'classrooms',model: 'cr_id', idField: 'cr_id',textField:'room_name', title: '教室',default: 0},
{com: 'SelectClass',model: 'cid',title: '班级',default: 0}
]

export default{
mixins: [ common, modal, globals ],
directives: {
TransferDom
},
components: {
Tag,
Popup,
ArrangeFilter
},
data () {
return {
data: [],
dictList: dictList,
currentItem: {},
search_field: {
lid: 0,
sj_id: 0,
cr_id: 0,
cid: 0
}
}
},
computed: {
data_uri () {
return 'course_arranges'
},
style () {
return {
zIndex: 1000
}
},
hasFilter () {
return this.search_field.lid>0||this.search_field.cr_id>0||this.search_field.sj_id>0
}
},
methods: {
deal_data (response) {
let list = response.body.data.list,
total = response.body.data.total,
curLength = 0

if(this.pageIndex===1){
this.data = []
}



this.data = this.data.concat(list)

this.mescroll.endBySize(list.length, total)
},
setCurrentRow (item) {
if(item.is_prepare){
return false
}
if(!this.$refs.filter.modal$.show){
this.currentItem = item
this.$emit('on-selected',item)
setTimeout(()=>{
this.close()
},500)
}
},
resetFilter () {
this.search_field.lid = 0
this.search_field.cid = 0
this.search_field.sj_id = 0
this.search_field.cr_id = 0
this.downCallback()
},
showFilter () {
this.$refs.filter.show()
},
getObjectType (item) {
let map_tag = {
0:{text:'班',color:'green'},
1:{text:'一',color:'blue'},
2:{text:'多',color:'yellow'},
3:{text:'试',color:'yellow'}
}
if(item.is_trial===1){
return map_tag[3]
}else{
return map_tag[item.lesson_type]
}
},
getObject (item) {
let result = ''

if(item.lesson_type===0){
if(item.is_trial===1||item.is_makeup===1){
result = item.name
}else{
if(item.one_class!=null){
result = item.one_class.class_name
}
}
}else{
let students = item.students||[]
students = students.filter(s=>s.sid>0)
if(students.length>0){
students.forEach((s,i)=>{
if(i<3){
result += s.student.student_name+','
}
})
}

result = result.substring(0,result.length-1)

if(students.length>3){
result += '等'
}
}

return result
},
hook_get_param (params) {
params.with = 'one_class,students'
params.is_attendance = '[In,0]'
let search_obj = util.copy(this.search_field)

params.teach_eid = this.eid$
params.is_trial = 0
params.order_field = 'int_day'
params.order_sort = 'asc'

for(let o in search_obj) {
let property = search_obj[o]
if(o=='int_day'){
if(property!=','&&property.length>0){
params[o] = util.sprintf('[Between,%s]',property.join(','))
}
}else if (o=='lesson_type'){
if(property.length>0){
params[o] = util.sprintf('[In,%s]',property.join(','))
}
}else{
if(property&&property!='-1'){
params[o] = property
}
}
}
}
}

}



